.oni-helper-hidden {
  display: none; }

.oni-helper-hidden-accessible {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.oni-helper-reset {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  line-height: 1.3;
  text-decoration: none;
  font-size: 100%;
  list-style: none; }

.oni-helper-noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none; }
  .oni-helper-noselect img, .oni-helper-noselect a {
    -webkit-user-drag: none;
    pointer-events: none; }

.oni-helper-clearfix {
  *zoom: 1; }
  .oni-helper-clearfix:after {
    content: "\0020";
    display: block;
    height: 0;
    clear: both;
    overflow: hidden;
    visibility: hidden; }

html .oni-helper-max-index, body .oni-helper-max-index {
  z-index: 1000; }

